.loader {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
  //position: absolute;
  //top: 50%;
  //left: 40%;
  //margin-left: 10%;
  //transform: translate3d(-50%, -50%, 0);
}
.dot {
  width: 18px;
  height: 18px;
  background: #3ac;
  border-radius: 100%;
  display: inline-block;
  animation: slide 1s infinite;
}
.dot:nth-child(1) {
  animation-delay: 0.1s;
  background: #32aacc;
}
.dot:nth-child(2) {
  animation-delay: 0.2s;
  background: #64aacc;
}
.dot:nth-child(3) {
  animation-delay: 0.3s;
  background: #96aacc;
}
.dot:nth-child(4) {
  animation-delay: 0.4s;
  background: #c8aacc;
}
.dot:nth-child(5) {
  animation-delay: 0.5s;
  background: #faaacc;
}

@keyframes slide {
  0% {
    transform: scale(1);
  }
  50% {
    opacity: 0.4;
    transform: scale(1.8);
  }
  100% {
    transform: scale(1);
  }
}
